.sheet {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.sheet .cell {
  /* flex: 0 0 25%; */
  /* max-width: 25%; */
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.sheet .cell-header {
  height: 20px;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 2px;
}

.sheet .cell-footer {
  height: 18px;
}

.sheet .cell-body {
  overflow: auto;
  display: -ms-flexbox;
  display: flex;
  height: 38px;
  border-right: 1px solid black;
}

.sheet .measure {
  -ms-flex: auto;
      flex: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
  margin: 0px;
  font-size: 1.5em;
  display: -ms-flexbox;
  display: flex;
}

.sheet .cell.block-start .cell-header {
  margin-left: 12px;
}

.sheet .space {
  display: block;
  width: 12px;
  height: 100%;
}

.measure .signs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.cell.block-start .measure {
  border-left: 1px solid black;
}

.cell .house {
  border-left: 1px solid black;
  border-top: 1px solid black;
  padding: 0 2px;
  width: 80%;
}

.cell .section {
  padding: 0 2px;
  border: 1px solid black;
  margin-right: 5px;
}

.sheet .top {
  width: 100%;
}

.sheet .measure>.chord {
  -ms-flex: 1 0 25%;
      flex: 1 0 25%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  /* padding-top: 12px; */
}

.chord-name {
  opacity: 1;
}

.measure.is-active {
  border-bottom: 1px solid black;
  border-top: 1px solid black;
  /* color: white; */
}

.color-bar {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}

.chord-name {
  white-space: nowrap;
  padding-left: 5px;
}


