ul {
    padding: 0;
    /* border-top: 1px solid #ccc; */
}

.explorer {
    height: calc(100% - 50px);
    width: 100%;
    position: absolute;
    top: 50px;
    display: -ms-flexbox;
    display: flex;
}

.explorer li, .song li {
    list-style: none;
    display: inline-block;
    min-width: 60px;
    margin: 2px;
    padding: 5px;
    cursor: pointer;
    white-space: nowrap;
    border-radius: 5px;
    -webkit-transition: background .2s linear;
    -o-transition: background .2s linear;
    transition: background .2s linear;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.action-buttons {
    float: right;
}

.aside {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 30vw;
    height: 100%;
    overflow: hidden;
    background: white;
    padding: 10px;
    background: #efefef;
}

.aside .circle {
    height: 250px;
    width: 100%;
    border-radius: 100%;
    text-align: center;
}

.aside .scroll-content {
    overflow: auto;
    height: calc(100% - 250px);
}

.explorer-content {
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 70vw;
    margin-left: 30vw;
    height: 100%;
    position: absolute;
    top: 0;
    overflow: auto;
}

.symbols {
    display: inline-block;
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -ms-flex-positive: 1;
        flex-grow: 1;
    -ms-flex-preferred-size: auto;
        flex-basis: auto;
    max-height: 100%;
    width: 100%;
}

/* .Circle {
    width: 100%;
} */

h5 {
    margin: 0;
}

ul {
    margin: 5px 0;
}

ul.scroll {
    height: 40px;
    white-space: nowrap;
    overflow: auto;
}

li:hover {
    text-decoration: underline;
}

